
.peep-root {
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 24px;
  box-sizing: border-box;
}

.peep-dashboard {
  width: min(1100px, calc(100% - 48px));
  max-width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0;
  padding: 18px;
  color: #d6f3d2;
  background: rgba(2, 8, 2, 0.6);
  backdrop-filter: blur(18px) saturate(120%);
  -webkit-backdrop-filter: blur(18px) saturate(120%);
  border-radius: 18px;
  box-shadow: 0 18px 50px rgba(0, 0, 0, 0.45);
  border: 1px solid rgba(132, 255, 132, 0.3);
}

.top-shell {
  border: 1px solid #1f441f;
  border-radius: 12px;
  padding: 16px;
  background: rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.title-row {
  display: flex;
  align-items: center;
  gap: 16px;
}

.title-block {
  display: flex;
  flex-direction: column;
  flex: 1;
}

h1 {
  margin: 0;
  font-size: 1.3rem;
  color: #f0fff0;
}

.subtitle {
  margin: 0;
  font-size: 0.75rem;
  color: #85b885;
}

.header-info {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
}

.info-chip {
  background: rgba(255, 255, 255, 0.07);
  border-radius: 8px;
  padding: 4px 10px;
  font-size: 0.8rem;
}

.title-row .refresh-btn {
  margin-left: auto;
}

.refresh-btn {
  background: #0f380f;
  border: 1px solid #4caf50;
  border-radius: 6px;
  color: #a3fda3;
  padding: 6px 14px;
  font-weight: 600;
  cursor: pointer;
}

.refresh-btn:hover {
  background: #1d5a1d;
}

.content-area {
  width: 100%;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
}

.tab-content {
  width: 100%;
  border: 1px solid #1f441f;
  border-radius: 12px;
  padding: 20px;
  background: rgba(0, 0, 0, 0.45);
  backdrop-filter: blur(12px);
  -webkit-backdrop-filter: blur(12px);
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  height: 100%;
  box-shadow: inset 0 0 30px rgba(0, 0, 0, 0.35);
  overflow: hidden;
}

.tab-content.scrollable {
  overflow-y: auto;
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  margin-bottom: 12px;
}

h2 {
  margin: 0;
  color: #a4ffde;
  font-size: 1.3rem;
}

.sub-text {
  margin: 0;
  color: #7da47d;
  font-size: 0.85rem;
}

.label {
  font-size: 0.8rem;
  color: #7a9d7a;
  margin: 0;
}

.value {
  font-size: 1.1rem;
  margin: 0;
  color: #f0fff0;
}

.value.danger {
  color: #ff7070;
}

.table-wrapper {
  overflow-x: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  font-size: 0.9rem;
}

.character-row {
  cursor: pointer;
}

.characters-tab {
  overflow: visible;
  flex: 1 1 auto;
  height: 100%;
  width: 100%;
}

th,
td {
  border-bottom: 1px solid #1d301d;
  padding: 8px;
  text-align: left;
}

tbody tr:hover {
  background: rgba(66, 134, 66, 0.12);
}

.bar {
  height: 6px;
  background: var(--bar-color);
  border-radius: 3px;
  margin-bottom: 4px;
  transition: width 0.3s ease;
}

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
  padding: 24px;
}

.modal-card {
  width: min(480px, 100%);
  background: rgba(5, 15, 5, 0.95);
  border: 1px solid #2c582c;
  border-radius: 16px;
  padding: 20px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.6);
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 16px;
}

.modal-header h3 {
  margin: 0;
  color: #a4ffde;
}

.modal-location {
  margin: 4px 0 0;
  font-size: 0.85rem;
  color: #7da47d;
}

.modal-close {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: #f0fff0;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  font-size: 1.2rem;
  cursor: pointer;
}

.modal-close:hover {
  background: rgba(255, 255, 255, 0.2);
}

.modal-content {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.modal-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
  padding: 8px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.06);
}

.modal-row:last-child {
  border-bottom: none;
}

.modal-label {
  color: #7da47d;
  font-size: 0.85rem;
}

.modal-value {
  color: #f0fff0;
  font-weight: 600;
}

.modal-row.meter {
  align-items: center;
}

.meter-bar {
  flex: 1;
  background: rgba(255, 255, 255, 0.08);
  border-radius: 4px;
  padding: 4px;
}

.meter-bar .bar {
  margin: 0;
  height: 8px;
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.status-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(160px, 1fr));
  gap: 16px;
}

.status-card {
  border: 1px solid #2c582c;
  border-radius: 12px;
  padding: 16px;
  background: rgba(5, 15, 5, 0.7);
}

.list-grid {
  list-style: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 16px;
}

.list-card {
  border: 1px solid #234b23;
  border-radius: 12px;
  padding: 14px;
  background: rgba(5, 20, 5, 0.6);
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 0.9rem;
}

.camera-card {
  cursor: pointer;
  transition: background 0.2s ease;
}

.camera-card:hover {
  background: rgba(18, 60, 18, 0.8);
}

.list-title {
  font-weight: bold;
  color: #efffe3;
  margin-bottom: 4px;
}

.empty {
  grid-column: 1 / -1;
  text-align: center;
  color: #5f7e5f;
  font-style: italic;
  border: 1px dashed #3f6a3f;
  border-radius: 10px;
  padding: 16px;
}

.tab-bar {
  margin-top: 0;
  display: flex;
  gap: 10px;
  border: 1px solid #1f441f;
  border-radius: 12px;
  padding: 10px;
  background: rgba(0, 0, 0, 0.35);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}

.tab-btn {
  flex: 1;
  min-width: 120px;
  border: none;
  border-radius: 999px;
  padding: 10px 16px;
  color: #9bcf9b;
  background: rgba(255, 255, 255, 0.04);
  font-size: 0.95rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.tab-btn.active {
  color: #071706;
  background: linear-gradient(90deg, #74ffba, #53c781);
  box-shadow: 0 6px 18px rgba(84, 196, 129, 0.25);
}

.camera-modal .modal-content p {
  margin: 0;
  line-height: 1.6;
}

@media (max-width: 640px) {
  .tab-bar {
    flex-direction: column;
    gap: 8px;
  }

  .tab-btn {
    min-width: 0;
    width: 100%;
  }
}
